footer p {
  padding: 0 1em;
  margin: 0;
  color: #4d4d4d;
}

.selectionContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  /*height: 80vh;*/
  padding: 3em 0;
}

.selection {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  height: 80px;
  background-color: #e1e7eb;
  border-bottom: 0.2em solid #d3d2d3;
  /*border-top: none;*/
  color: black;
  text-decoration: none;
  font-size: 12px;
}

.selection img {
  width: 7em;
  height: 5em;
}

.details {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin-left: 0.5em;
}

@media (min-width: 520px) {
  .selection {
    margin: 0.2em 0;
    padding: 1em;
    height: 100px;
  }
  .selection img {
    width: 9em;
    height: 7em;
  }
  .details {
    font-size: 18px;
  }
  .selectionContainer {
    padding: 5em 0 0 0;
  }
}



